import { Component } from '@angular/core';
import { IonicPage, NavController} from 'ionic-angular';
import { GlobalComponent } from "../../components/global/global";
import { SchServicesProvider } from '../../providers/sch-services/sch-services';
/**
 * Generated class for the SchPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-sch',
  templateUrl: 'sch.html',
})
export class SchPage {

  constructor(public navCtrl: NavController,
    public global: GlobalComponent,
     public SchServices:SchServicesProvider,) {
  }
  openNavSqlPage(item, method: string) {
    this.global.createLoader("連線中...");
    this.global.loading.present().then(() => {
      this.SchServices.GetSqlAsync(item.sch_no, method).subscribe(
        data => {
          if (data.DidError === true) {
            this.global.dismissLoading();
            this.global.showError(data.ErrorMessage);
          } else {
            this.navCtrl.push("SqlPage", {
              sql_statement: data.Model,
              is_exec: false
            });
            this.global.dismissLoading();
          }
        },
        err => {
          this.global.dismissLoading();
          this.global.showError("無法連上WebAPI伺服器-"+err.message);
        }
      );
    });
  }

  openNavJobPage(item) {
    this.global.createLoader("連線中...");
    this.global.loading.present().then(() => {
      this.navCtrl.push("JobBySchPage", { item: item });

      });
  }
  ionViewDidLoad() {
    console.log('ionViewDidLoad SchPage');
    }

}



